.contact .contact-input {
  margin-left: 10px !important;
}

.contact .contact-button {
  color: white !important;
  background-color: black !important;
  display: block !important;
  position: relative;
  left: 21vw;
  top: 4vh;
 box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1)!important;
}
.contact .contact-button:hover
{
  background-color: white!important;
 color: black!important;
 box-shadow: 0px 15px 20px rgba(179, 179, 179, 0.4)!important;
}
.contact .contact-from {
  /* width: 50vw!important; */
  display: inline;
  
}

.contact .contact-information {
  height: 30rem;
  float: left;
  color: #fff;
  border-radius: 15px;
  background-color: #171717;
  background-image: url("https://www.transparenttextures.com/patterns/grey-washed-wall.png");
  margin-right: 25px;
}
.contact .contact-information ul {
  list-style-type: none;
}
.contact .contact-information ul li {
  margin: 35px 0;
}
/* .contact .contact-information h2 {
  font-weight: bold;
} */
/* .contact-information p,
h2 {
  margin-left: 25px;
  line-height: 1.5;
} */
.contact-information ul li span {
  margin-left: 5%;
  position: relative;
  bottom: 7px;
}
.contact-information .social-media {
  text-align: center;
  margin-top: 100px;
}
.contact-information .social-media-icon {
  margin: 0 10px;
  color: white !important;
}
.contact-email {
  margin: 2% 0 3% 0;
}
.message {
  width: 30vw;
}
.contact {
  margin: 5% 10% 0 12%;
}
@media (max-width: 1104px) and (min-width: 736px) {
  .contact {
    margin: 5% 0;
  }
  .message {
    width: 40vw;
  }
  .contact-button {
    color: white !important;
    background-color: black !important;
    display: block !important;
    position: relative;
    left: 1vw;
    top: 4vh;
  }
  .contact-information {
    height: 30rem;
    float: left;
    color: #fff;
    border-radius: 15px;
    background-color: #171717;
    background-image: url("https://www.transparenttextures.com/patterns/grey-washed-wall.png");
    margin-right: 0;
    width: 39vw;
  }
  .contact-from {
    width: 30vw!important;
}
}
@media (max-width: 736px) {
    .contact {
      margin: 5% 0;
      
    }
    .message {
      width: 80vw;
    }
    .contact-button {
      color: white !important;
      background-color: black !important;
      display: block !important;
      position: relative;
      left: 1vw;
      top: 4vh;
    }
    .contact-information {
      display: none;
    }
    .contact-from {
        width: 25vw!important;
  }
}

