.image {
  float: left;
  padding-right: 11%;
}
.description {
  margin-left: 11%;
  font-size: 1.19rem;
  text-align: inherit;
}
.description h6 {
  position: relative;
  bottom: 7px;
  line-height: 1.2;
}
.description h5 {
  position: relative;
  top: 12px;
  line-height: 1.2;
}
