.search-from {
  width: 100vw;
  height: 4.6rem;
  background-color: #f3f3f4;
  margin-left: -8px !important;
  margin-bottom: 10%;
}
.not-found
{
    text-align: center;
    margin: auto;
    width: 50%;
}
.search-label {
  margin: auto !important;
  width: 35vw !important;
  padding: 15px 0 !important;
  display: inherit !important;
  background-color: white;
  position: relative;
  top: 40px;
  border-radius: 5px;
  padding: 0;
  border: 0 !important;
  box-shadow: 0px 15px 20px rgba(179, 179, 179, 0.4) !important;
}
.search-icon {
  padding: 0 15px;
}
.lookup-button {
  background-color: black !important;
  color: white !important;
  padding: 20px 1.5vw 20px 1.5vw !important;
}
@media (max-width: 678px) and (min-width: 601px) {
  .search-label {
    margin: auto !important;
    width: 60vw !important;
    padding: 10px 0 !important;
    display: inherit !important;
    background-color: white;
    position: relative;
    top: 40px;
    border-radius: 5px;
    padding: 0;
    border: 0 !important;
    box-shadow: 0px 15px 20px rgba(179, 179, 179, 0.4) !important;
  }
  .lookup-button {
    background-color: black !important;
    color: white !important;
    padding: 16px 1.5vw 16px 1.5vw !important;
  }
  .search-from {
    width: 100vw;
    height: 4.2rem;
    background-color: #f3f3f4;
    margin-left: -8px !important;
  }
}
@media (max-width: 600px) {
  .search-label {
    margin: auto !important;
    width: 70vw !important;
    padding: 6px 0 !important;
    display: inherit !important;
    background-color: white;
    position: relative;
    top: 40px;
    border-radius: 5px;
    padding: 0;
    border: 0 !important;
    box-shadow: 0px 15px 20px rgba(179, 179, 179, 0.4) !important;
  }
  .lookup-button {
    background-color: black !important;
    color: white !important;
    padding: 10px 1.5vw 10px 1.5vw !important;
  }
  .search-from {
    width: 100vw;
    height: 4.1rem;
    background-color: #f3f3f4;
    margin-left: -8px !important;
    margin-bottom: 20%;
  }
}
